'use client'
import { useAuth } from "@/components/AuthContext" 
import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { DeleteIcon, Edit, MoreHorizontal } from "lucide-react"
import { bookService } from "@/lib/services/bookService"
import { Skeleton } from "@/components/ui/skeleton"
import { DropdownMenu } from "@radix-ui/react-dropdown-menu"
import { DropdownMenuContent, DropdownMenuLabel, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
import { Button } from "@/components/ui/button"


export default function Page(){
    const {isAuthenticated} = useAuth()
    const router = useRouter()
    const [booksData, setBooksData] =useState([])
    const [isLoading,setIsLoading] = useState(false)

    useEffect(()=>{
       if(!isAuthenticated){
            router.push('/login')
        }
    },[isAuthenticated])

    useEffect(()=>{
       setIsLoading(true)
       bookService.getBooks().then((res)=>{
        setIsLoading(false)
        setBooksData(res.data.data)
       }).catch(error=>{
        setIsLoading(false)
        console.log(error)
       })   
    },[])

    const handleDeleteBook = async(id:string)=>{
      setIsLoading(true)
      bookService.deleteBook(id).then((res)=>{
        setIsLoading(false)
        setBooksData((prev)=>{
          return prev.filter((book:any)=>book?.id !== id)
        })
      }).catch(error=>{
        setIsLoading(false)
        console.log(error)
      })
    }
    return(
        <div className="w-full">
            <div className="flex justify-between items-center px-12 py-4">
              <input className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" 
              placeholder="search"
              />
              <Button className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded-lg" >Add Book</Button>
            </div>
            <div className="flex justify-center">
<Table className="bg-white">
  <TableCaption>A list of your recent book.</TableCaption>
  <TableHeader>
    <TableRow>
      <TableHead className="">ID</TableHead>
      <TableHead>Title</TableHead>
      <TableHead>Auther</TableHead>
      <TableHead className="">Publish Date</TableHead>
      <TableHead className="">Owner</TableHead>
      <TableHead className="">Action</TableHead>
    </TableRow>
  </TableHeader>
  <TableBody>
    {isLoading ? (
      <>
      <Skeleton className="h-[20px] w-full " />
      <Skeleton className="h-[20px] w-full " />
      <Skeleton className="h-[20px] w-full " />
      <Skeleton className="h-[20px] w-full " />
      </>
    ):
    (
      booksData.length && booksData.map((book:any,index)=>{
        return(
          <TableRow key={index}>
      <TableCell className="">{book?.id}</TableCell>
      <TableCell>{book?.title}</TableCell>
      <TableCell>{book?.author}</TableCell>
      <TableCell>{book.published_date}</TableCell>
      <TableCell className="">{book.owner}</TableCell>
      <TableCell className = "">
        <DropdownMenu>
          <DropdownMenuTrigger>
            <MoreHorizontal className="h-5 w-5 text-gray-400"/>
            </DropdownMenuTrigger>
          <DropdownMenuContent className="w-18">
             
             <DropdownMenuLabel className="flex items-center text-gray-700 cursor-pointer">
              <Popover >
                <PopoverTrigger className="flex items-center text-gray-700 cursor-pointer">
                   <DeleteIcon className="mr-4 text-[12px]"/>
              Delete
                </PopoverTrigger>
                <PopoverContent className="w-64">
                  are you sure to delete this book?
                  <div className="flex justify-center mt-4">
                      <Button className="text-red-600 bg-transparent outline-red-600">No</Button>
                      <Button className="text-green-600 bg-transparent outline-green-600" onClick={()=>handleDeleteBook(book.id)}>Yes</Button>
                  </div>
                  
                </PopoverContent>
              </Popover>
             </DropdownMenuLabel>
             <DropdownMenuLabel className="flex item-center text-gray-700 cursor-pointer">
              <Edit className="mr-4 text-[12px]"/>
              Update</DropdownMenuLabel>
          </DropdownMenuContent>
        </DropdownMenu>
        </TableCell>
    </TableRow>
        )
      })
    
    )}
  </TableBody>
</Table>
            </div>
            
        </div>
    )
}